import request from '@/utils/request'

// 获取带书籍数量的父分类
export function fetchCategories() {
    return request({
      url: '/categories',
      method: 'get'
    })
}
// 获取带子分类的分类
export function fetchsubCategories() {
  return request({
    url: '/sub-categories',
    method: 'get'
  })
}
// 获取书籍详情
export function fetchcategoryInfo(query) {
  return request({
    url: '/category-info',
    method: 'get',
    params: query
  })
}