<template>
    <div class="categories">
        <van-tree-select
            height="100vh"
            :items="items"
            :main-active-index.sync="activeIndex"
            class="select"
        >
            <template slot="content">
                <rank-list v-if="activeIndex === 0" title="男生" :list="male" gender="male"/>
                <rank-list v-if="activeIndex === 1" title="女生" :list="female" gender="female"/>
                <rank-list v-if="activeIndex === 2" title="标签" :list="picture" gender="picture"/>
            </template>
        </van-tree-select>
    </div>
</template>

<script>
import { fetchCategories } from '@/api/categories'
import { RankList } from '@/components'
export default {
    name: 'Categories',
    data() {
        return {
            activeIndex: 0,
            items: [{ text: '男生' }, { text: '女生' }, { text: '标签' }],
            male: [],
            female: [],
            picture: []
        }
    },
    components: {
        RankList
    },
    created () {
        this.getCategories()
    },
    methods: {
        getCategories() {
            fetchCategories().then(res => {
                this.male = res.data.male
                this.female = res.data.female
                this.picture = res.data.picture
            })
        }
    }
}
</script>

